<template>
  <form @submit.pr.prevent="onSubmit">
    <input type="text" v-model="title">
    <button type="submit">Create</button>
  </form>
</template>

<script>
export default {
  data(){
    return{
      title: ''
    }
  },
  name: "AddTodo",
  methods:{
    onSubmit()
    {
      console.log('submit', this.title)
      if(this.title.trim()){
        const newTodo ={
          id: Date.now(),
          title: this.title,
          completed: false
        }
        this.$emit('add-todo', newTodo)
        this.title = ''
      }
    }
  }
}
</script>

<style scoped>
  form{
    display: flex;
  }

  input{
    width: 400px;
  }
</style>